* {
    font-family: 'Roboto', sans-serif;
}

.app {
    display: flex;
    width: 100%;
    height: 80%;
    flex-direction: row;
    position: absolute;
    justify-content: center;
    align-items: center;
    margin: auto;
}

.input_group {
    display: flex;
    flex-direction: column;
    padding: 0.5rem;
    font-size: 24px;
}

.input_group-text {
    font-size: 18px;
}